.b-plans {}
.b-plans__header {
    @include wrapper();
    margin-bottom: 90px;
    position: relative;
    padding-bottom: 30px;
    &:after {
        content: '';
        width: 30%;
        height: 2px;
        @include absolute-offset(auto, auto, 0, 50%, 0, -50%);
        background-image: linear-gradient(90deg, #b2a56e, #f2e2ad, #b2a56e); } }
.b-plans__title {
    position: relative;
    text-align: center;
    text-transform: uppercase;
    @include text($lightColor, rem(60px), 900, 1);
    @include lg-block() {
        @include text($lightColor, rem(48px), 900, 1); }
    @include sm-block() {
        @include text($lightColor, rem(36px), 900, 1); }
    &:before {
        content: attr(data-second-text);
        z-index: -1;
        opacity: .8;
        @include text(darken($darkColor, 20%), rem(440px), 900, 1);
        @include absolute-offset(50%, auto, auto, 50%, -40%, -50%);
        @include lg-block() {
            @include text(darken($darkColor, 20%), rem(360px), 900, 1); }
        @include sm-block() {
            @include text(darken($darkColor, 20%), rem(220px), 900, 1); } } }

.b-plans__title-thin {
    display: inline-block;
    font-weight: 100; }

.b-plans__main {
    background-color: rgba(0,0,0,.2); }
.b-plans__inner {
    @include wrapper(); }
.b-plans__items {
    @include row-flex();
    margin: -10px 0 -10px 0;
    position: relative; }
.b-plans__item {
    @include col();
    @include size(4);
    @include size-lg(12);
    margin-top: 10px;
    margin-bottom: 10px;
    @include sm-block() {
        margin-top: 5px;
        margin-bottom: 5px; } }

.b-plans__item_scale {
    z-index: 1;
    transform: scale(1.2);
    @include lg(transform, scale(1.12)); }
