.b-page {
    width: 100%;
    height: auto;
    max-width: 100%;
    min-width: 320px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    overflow: hidden;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../img/main-bg.jpg");
    padding-bottom: 200px;
    @include lg(padding-bottom, 100px);
    &:after {
        content: '';
        width: 100%;
        height: 100%;
        @include absolute(0, 0, auto, auto);
        opacity: .6;
        background-position: 50% 75%;
        background-repeat: no-repeat;
        background-image: url("../img/abstraction-bg.png"); } }



.b-page__header {
    width: 100%;
    height: auto;
    min-height: 160px;
    z-index: 3;
    @include sm(min-height, 80px); }


.b-page__header_absolute {
    position: absolute;
    top: 0;
    left: 0; }


.b-page__header_absolute-lg {
    @include from($break_sm);
    position: absolute;
    top: 0;
    left: 0; }


.b-page__header_absolute-sm {
    @include to($break_sm);
    position: absolute;
    top: 0;
    left: 0; }


.b-page__main {
    width: 100%;
    height: auto;
    z-index: 1; }


.b-page__footer {
    width: 100%;
    height: auto;
    z-index: 2; }
