// Position
@mixin absolute($top, $right, $bottom, $left) {
    position: absolute;
    top: $top;
    bottom: $bottom;
    left: $left;
    right: $right; }

@mixin absolute-offset($top, $right, $bottom, $left, $offsetTop, $offsetLeft) {
    position: absolute;
    top: $top;
    bottom: $bottom;
    left: $left;
    right: $right;
    transform: translate($offsetLeft, $offsetTop); }

@mixin absolute-offset-plus($top, $right, $bottom, $left, $offsetTop, $offsetLeft, $transform) {
    position: absolute;
    top: $top;
    bottom: $bottom;
    left: $left;
    right: $right;
    transform: $transform translate($offsetLeft, $offsetTop); }


@mixin translate($top, $left) {
    transform: translate($left, $top); }


// Typography
@mixin text($color, $fontSize, $fontWeight, $lineHeight) {
    color:  $color;
    font-size: $fontSize;
    font-weight: $fontWeight;
    line-height: $lineHeight; }


// Interactive
@mixin hover($property, $value, $speed, $function) {
    cursor: pointer;
    transition: all $speed $function;
    &:hover {
        #{$property}: $value; } }


@mixin hover-block($speed, $function) {
    cursor: pointer;
    transition: all $speed $function;
    &:hover {
        @content; } }


@mixin focus($property, $value, $speed, $function) {
    cursor: pointer;
    transition: all $speed $function;
    &:focus {
        #{$property}: $value; } }


@mixin focus-block($speed, $function) {
    cursor: pointer;
    transition: all $speed $function;
    &:hover {
        @content; } }


@mixin active($property, $value, $speed, $function) {
    cursor: pointer;
    transition: all $speed $function;
    &:active {
        #{$property}: $value; } }


@mixin active-block($speed, $function) {
    cursor: pointer;
    transition: all $speed $function;
    &:active {
        @content; } }
