.b-plan {
    padding: 60px 30px 60px 80px;
    background-color: $darkColor;
    position: relative;
    box-shadow: 0 20px 20px rgba(0,0,0,.2);
    @include sm(padding, 30px 15px 30px 50px); }

.b-plan_accent {
    background-color: lighten($darkColor, 2%);
    box-shadow: 0 20px 40px rgba(0,0,0,.2); }

.b-plan__title {
    @include absolute-offset-plus(60px, calc(100% - 40px), auto, auto, -50%, 0, rotate(-90deg));
    white-space: nowrap;
    transform-origin: right top;
    @include text($lightColor, rem(34px), 900, 1);
    @include sm-block() {
        @include text($lightColor, rem(22px), 900, 1);
        @include absolute-offset-plus(30px, calc(100% - 25px), auto, auto, -50%, 0, rotate(-90deg)); } }

.b-plan__title_accent {
    background: -webkit-linear-gradient(90deg, #b2a56e, #f2e2ad, #b2a56e);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.b-plan__top {
    margin-bottom: rem(30px);
    @include sm(margin-bottom, rem(20px)); }

.b-plan__duration {
    display: inline-block;
    margin-right: rem(4px);
    @include text($lightColor, rem(18px), 900, 1);
    @include sm-block() {
        margin-right: rem(2px);
        @include text($lightColor, rem(14px), 900, 1); } }

.b-plan__save {
    display: inline-block;
    @include text($lightColor, rem(18px), 100, 1);
    @include sm-block() {
        @include text($lightColor, rem(14px), 100, 1); } }

.b-plan__price {
    position: relative;
    margin-bottom: rem(30px);
    @include sm(margin-bottom, rem(20px)); }

.b-plan__price-first {
    display: inline-block;
    @include text($lightColor, rem(36px), 900, 1);
    @include sm-block() {
        @include text($lightColor, rem(28px), 900, 1); } }

.b-plan__price-first_accent {
    background: -webkit-linear-gradient(90deg, #b2a56e, #f2e2ad, #b2a56e);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.b-plan__price-first-sm {
    display: inline-block;
    @include text($lightColor, rem(18px), 900, 1);
    @include sm-block() {
        @include text($lightColor, rem(14px), 900, 1); } }

.b-plan__price-first-sm_accent {
    background: -webkit-linear-gradient(90deg, #b2a56e, #f2e2ad, #b2a56e);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.b-plan__price-second {
    @include absolute-offset(50%, auto, auto, 0, -50%, 0);
    @include text($lightColor, rem(60px), 900, 1);
    opacity: .1;
    @include sm-block() {
        @include text($lightColor, rem(46px), 900, 1); } }

.b-plan__price-second-sm {
    display: inline-block;
    @include text($lightColor, rem(32px), 900, 1); }

.b-plan__discount {
    @include text($lightColor, rem(14px), 900, 1);
    margin-bottom: rem(30px);
    @include sm-block() {
        margin-bottom: rem(15px);
        @include text($lightColor, rem(12px), 900, 1); } }

.b-plan__discount-old {
    display: inline-block;
    text-decoration: line-through;
    margin-right: rem(4px);
    @include text($lightColor, rem(14px), 100, 1);
    @include sm-block() {
        @include text($lightColor, rem(12px), 100, 1); } }

.b-plan__separator {
    width: 50%;
    height: rem(1px);
    background-color: $lightColor;
    margin-bottom: rem(30px);
    @include sm(margin-bottom, rem(15px)); }
.b-plan__form {}
.b-plan__input {
    display: none; }
.b-plan__submit {
    @include text($lightColor, rem(24px), 900, 1);
    background: none;
    border: none;
    outline: none;
    @include sm-block() {
        @include text($lightColor, rem(18px), 900, 1); } }
